<template>
    <div class="model">
        <el-card class="box-card">
            <el-table :data="model_data" style="width: 100%">
                <el-table-column
                    prop="model_id"
                    label="模型ID">
                </el-table-column>
                <el-table-column
                    prop="create_time"
                    label="模型创建时间">
                </el-table-column>
                <el-table-column
                    label="备注">
                    <template slot-scope="scope">
                        <el-input
                            type="textarea"
                            autosize
                            v-model="scope.row.alias"
                            placeholder="暂无"
                            @change="changeAlias(scope.row)">
                        </el-input>
                    </template>
                </el-table-column>
                <el-table-column label="操作">
                    <template slot-scope="scope">
                        <el-popconfirm
                            title="确定要删除模型吗？"
                            class="el-popconfirm oper-btn"
                            @confirm="deleteModel(scope.row.model_id)">
                            <el-button
                                type="danger"
                                size="small"
                                slot="reference">
                                删除模型
                            </el-button>
                        </el-popconfirm>
                        <el-button
                            class="oper-btn"
                            type="primary"
                            size="small"
                            v-on:click="$router.push('/task_detail?task_id='+scope.row.model_id)" >
                            查看任务
                        </el-button>
                        <el-button
                            class="oper-btn"
                            type="warning"
                            :id = 'buttonRefAndID("loadAndUnload",scope.$index)'
                            size="small"
                            :ref='buttonRefAndID("loadAndUnload",scope.$index)'
                            v-on:click="clickLoadAndUnload(scope)" >
                          加载模型
                        </el-button>
                        <el-button
                            class="oper-btn"
                            type="info"
                            :id ='buttonRefAndID("predict",scope.$index)'
                            size="small"
                            :ref='buttonRefAndID("predict",scope.$index)'
                            v-on:click="clickPredictModel(scope)">
                          模型预测
                        </el-button>
                    </template>
                </el-table-column>
            </el-table>
            <el-pagination
                id="pagination"
                background
                :page-size="page_size"
                :page-count="page_count"
                :total="total_count"
                layout="prev, pager, next, jumper"
                @current-change="handleCurrentChange"
            >
            </el-pagination>
        </el-card>
      <el-dialog
          title="模型预测"
          :visible.sync="predictModelVisible"
          :close-on-click-modal='false'
          width="30%">
        <el-form ref="form" :model="predictDataForm" label-width="80px">
          <el-form-item label="属性/特征">
            <el-input type="textarea"  v-model="predictDataForm.headers"
                      placeholder="输入预测数据的id及特征，用逗号分割，如：id,x0,x1,x2,x3,x4"></el-input>
          </el-form-item>
          <el-form-item label="数据">
            <el-input type="textarea" :rows="6" v-model="predictDataForm.data"
                      placeholder="输入要预测的数据，用逗号分割，行之间用分号分割，或直接换行。
如：133,0.259,-1.033,0.256,0.014,-0.366;134,0.359,-1.233,0.226,0.414,-0.356
或者：
133,0.259,-1.033,0.256,0.014,-0.366
134,0.359,-1.233,0.226,0.414,-0.356"
            ></el-input>
          </el-form-item>
          <el-form-item label="预测结果">
            <el-input type="textarea" :rows="5" placeholder="在这里显示预测结果" v-model="predictDataForm.result"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button type="primary" @click="startPredictButton">预 测</el-button>
          <el-button @click="predictModelVisible = false">取 消</el-button>
        </span>
      </el-dialog>
    </div>
</template>

<script>
    export default {
        name: 'Model',
        data() {
            return {
                // 数据：模型数据
                model_data: [],
                // 数据：每页显示的 task 数量
                page_size: 10,
                page_count: 1,
                total_count: 1,
                curr_page: 1,
                // 任务的json
                task_json_dict: null,
                model_loader:null,
                model_list:null,
                //预测的头和数据
                predictHeaders:null,
                predictData:null,
                predictResult:null,
                predictModelId: null,
                predictModelVisible:false,
                //模型是否被加载
                is_load:[],
                //定时器
                timer:null,
                timer_load_unload:null,
                //预测数据的Form
                predictDataForm:{headers:'',data:'',result:''},
            };
        },
        mounted() {
            if (window.localStorage.getItem('jeddak_token') !== null) {
                this.getModelData(this.curr_page);
            }
        },
        beforeDestroy() {
          clearInterval(this.timer);
          clearInterval(this.timer_load_unload);
        },
        methods: {
            // 获取模型数据
             async getModelData(curr_page) {
                this.curr_page = curr_page;
                let payload = new URLSearchParams({
                    filter: JSON.stringify({
                        'deleted': false,
                    }),
                    page_size: this.page_size,
                    curr_page: curr_page,
                    order_by: '-create_time'
                })
               try{
                 const { data: resp } = await this.$axios.get("/board/models?"+payload.toString(),{
                   headers: {
                     'auth': window.localStorage.getItem('jeddak_token')
                   }
                 });
                 this.model_data = resp.data.models;
                 for (let i = 0; i < this.model_data.length; i++) {
                   this.model_data[i].create_time = new Date(this.model_data[i].create_time+'Z').toLocaleString();
                 }
                 this.total_count = resp.data.total_count;
                 this.page_count = Math.ceil(this.total_count/this.page_size);
               }catch (error) {
                 if (error.response) {
                   console.log(error.response.data);
                   console.log(error.response.status);
                   console.log(error.response.headers);
                   this.$notify.error({
                     title: '提示',
                     message: '操作失败，可能不具备权限，请尝试注销重新登录或联系管理员'
                   });
                   return;
                 }
                 console.log(error);
               }
               //更新加载/卸载模型按钮和预测按钮的颜色和文字
               await this.modelList();
               let l = 0;
               this.is_load = [];
               for(let i=0;i<this.model_data.length;i++){
                 let find = false;
                 for(let j=l;j<this.model_list.length;j++){
                   if(this.model_data[i].model_id === this.model_list[j]){
                     find = true;
                     l = j+1;
                     break;
                   }
                 }
                 this.is_load.push(find);
                 if(find){
                   this.changeToUnload(i);
                 }else {
                   this.changeToLoad(i);
                 }
               }
               return this.model_data;
            },

            // 删除模型
            deleteModel(model_id) {
                // send post to backend
                this.$axios.delete("/board/model/"+model_id, {
                    headers: {
                        'auth': window.localStorage.getItem('jeddak_token')
                    }
                })
                // eslint-disable-next-line no-unused-vars
                .then(resp => {
                    this.$notify.success({
                        title: '提示',
                        message: '模型删除成功，页面刷新中'
                    });
                    this.getModelData(this.curr_page);
                })
                .catch(err => {
                    console.log(err);
                    this.$notify.error({
                        title: '提示',
                        message: '操作失败，可能不具备权限，请尝试注销重新登录或联系管理员'
                    });
                });
            },

            // 修改备注名
            changeAlias(row) {
                this.$axios.patch("/board/model/"+row.model_id, {
                    'field': {
                        'alias': row.alias
                    },
                }, {
                    headers: {
                        'auth': window.localStorage.getItem('jeddak_token')
                    }
                })
                // eslint-disable-next-line no-unused-vars
                .then(resp => {
                    this.$notify.success({
                        title: '提示',
                        message: '修改成功'
                    });
                })
                .catch(err => {
                    console.log(err);
                    this.$notify.error({
                        title: '提示',
                        message: '操作失败，可能不具备权限，请尝试注销重新登录或联系管理员'
                    });
                });
            },

            // 分页跳转
            handleCurrentChange(val) {
                this.getModelData(val);
            },

            async loadModel(sc){
              // get task json
              try{
                const { data: resp } = await this.$axios.get("/board/task/"+sc.row.model_id+"?type=detail", {
                  headers: {
                    'auth': window.localStorage.getItem('jeddak_token')
                  }
                });
                this.task_json_dict = JSON.parse(resp.data.task_chain);
              }catch (error) {
                if (error.response) {
                  console.log(error.response.data);
                  console.log(error.response.status);
                  console.log(error.response.headers);
                  this.$notify.error({
                    title: '提示',
                    message: '该任务已删除'
                  });
                  return;
                }
                console.log(error);
              }
              if(this.task_json_dict !== null && this.task_json_dict.length!==0){
                let party_number = this.task_json_dict[0].party_names.length;
                let model_id = [];
                let action = [];
                for(let i=0;i<party_number;i++){
                  model_id.push(sc.row.model_id);
                  action.push('load');
                }
                let task_role = [];
                let find_task_role = false;
                for(let i=0;i<this.task_json_dict.length;i++){
                  // eslint-disable-next-line no-prototype-builtins
                  if(this.task_json_dict[i].hasOwnProperty('task_role')){
                    // task_role = this.task_json_dict[i].task_role;
                    for(let j=0;j<this.task_json_dict[i].task_role.length;j++){
                      task_role.push(this.task_json_dict[i].task_role[j]);
                    }
                    find_task_role = true;
                    break;
                  }
                }
                if(!find_task_role){
                  task_role.push('guest')
                  for(let i=1;i<party_number;i++){
                    task_role.push('host')
                  }
                }
                this.model_loader = {"task_type": "model_loader", "task_role": task_role, "model_id": model_id, "action": action};
              }
              this.task_json_dict[0].save_model = false;
              // 发起model load
              try{
                const { data: resp } = await this.$axios.post("/board/predict", {
                  'req_type': 'model_load',
                  'filter': [this.task_json_dict[0],this.model_loader]
                },{
                  headers: {
                    'auth': window.localStorage.getItem('jeddak_token')
                  }
                });
                console.log(resp);
                if(resp.status === 200){
                  this.$notify.info({
                    title: '提示',
                    message: '模型加载中'
                  });
                }
              }catch (error) {
                if (error.response) {
                  console.log(error.response.data);
                  console.log(error.response.status);
                  console.log(error.response.headers);
                  this.$notify.error({
                    title: '提示',
                    message: '加载模型失败'
                  });
                  return;
                }
                console.log(error);
              }
            },

            unloadModel(sc){
                this.$axios.post("/board/predict", {
                  "req_type":"model_unload",
                  "filter":
                      {
                        "model_id": sc.row.model_id
                      }
                }, {
                  headers: {
                    'auth': window.localStorage.getItem('jeddak_token')
                  }
                })
                // eslint-disable-next-line no-unused-vars
                .then(resp => {
                })
                .catch(err => {
                  console.log(err);
                  this.$notify.error({
                    title: '提示',
                    message: '卸载模型失败'
                  });
                });
            },

            //model list
            async modelList(){
                try{
                  const { data: resp } = await this.$axios.post("/board/predict", {
                    "req_type":"model_list"
                  }, {
                    headers: {
                      'auth': window.localStorage.getItem('jeddak_token')
                    }
                  });
                  this.model_list = resp.data;
                }catch (error) {
                  if (error.response) {
                    console.log(error.response.data);
                    console.log(error.response.status);
                    console.log(error.response.headers);
                    this.$notify.error({
                      title: '提示',
                      message: '获取加载模型列表失败'
                    });
                    return;
                  }
                  console.log(error);
                }
            },

            //预测模型
            async predictModel(id){
               this.predictDataForm.result = "正在预测中，请等待";
              try{
                const { data: resp } = await this.$axios.post("/board/predict", {
                  "req_type":"predict",
                  "filter": {
                        "model_id":id,
                        "input_data": [{"headers": this.predictHeaders, "data": this.predictData}, {}]
                  }
                }, {
                  headers: {
                    'auth': window.localStorage.getItem('jeddak_token')
                  }
                });
                this.predictResult = resp.data;
              }catch (error) {
                if (error.response) {
                  console.log(error.response.data);
                  console.log(error.response.status);
                  console.log(error.response.headers);
                  this.$notify.error({
                    title: '提示',
                    message: '获取加载模型列表失败'
                  });
                  return;
                }
                console.log(error);
              }
              console.log(this.predictResult['predict_data']);
              this.predictDataForm.result = JSON.stringify(this.predictResult['predict_data']);
            },

            //点击加载模型、卸载模型按钮触发的事件
            clickLoadAndUnload(sc){
                if(this.checkLoadAndUnloadStatus(sc.$index)){
                  this.loadModel(sc);
                  this.timer = window.setInterval(() => this.modelList(), 1000);
                  this.timer_load_unload = window.setInterval(() =>
                      this.isLoadInModelList(sc.row.model_id), 1000);
                }else {
                  this.unloadModel(sc);
                  this.timer = window.setInterval(() => this.modelList(), 500);
                  this.timer_load_unload = window.setInterval(() =>
                      this.isUnloadInModelList(sc.row.model_id), 500);
                }
            },

            //检测模型是否加载成功
            async isLoadInModelList(id){
              for(let i=0;i<this.model_list.length;i++){
                if(id === this.model_list[i]){
                  clearInterval(this.timer);
                  clearInterval(this.timer_load_unload);
                  await this.getModelData(this.curr_page);
                  this.$notify.success({
                    title: '提示',
                    message: '加载模型成功'
                  });
                }
              }
            },

            //检查是否卸载成功
            async isUnloadInModelList(id){
              let find = false;
              for(let i=0;i<this.model_list.length;i++){
                if(id === this.model_list[i]){
                  find = true;
                }
              }
              if(!find){
                clearInterval(this.timer);
                clearInterval(this.timer_load_unload);
                await this.getModelData(this.curr_page);
                this.$notify.success({
                  title: '提示',
                  message: '卸载模型成功'
                });
              }
            },

            //检查当前按钮展示的是加载模型还是卸载模型，为true为加载模型，为false为卸载模型
            checkLoadAndUnloadStatus(ind){
                let buttonRef = 'loadAndUnload' + ind;
                let text = this.$refs[buttonRef].$el.innerText;
                return text === '加载模型';
            },

            // 修改按钮的名字：加载模型----->卸载模型，同时模型预测变为绿色
            changeToUnload(ind){
                let buttonRef = 'loadAndUnload' + ind;
                this.$refs[buttonRef].$el.innerText = '卸载模型';
                let obj = document.getElementById(buttonRef);
                obj.style.backgroundColor = '#909399';
                obj.style.borderColor = '#909399';
                let predictRef = 'predict' + ind;
                obj = document.getElementById(predictRef);
                obj.style.backgroundColor = '#67C23A';
                obj.style.borderColor = '#67C23A';
            },

            // 修改按钮的名字：卸载模型----->加载模型，同时模型预测变为灰色
            changeToLoad(ind){
                let buttonRef = 'loadAndUnload' + ind;
                this.$refs[buttonRef].$el.innerText = '加载模型';
                let obj = document.getElementById(buttonRef);
                obj.style.backgroundColor = '#E6A23C';
                obj.style.borderColor = '#E6A23C';
                let predictRef = 'predict' + ind;
                obj = document.getElementById(predictRef);
                obj.style.backgroundColor = '#909399';
                obj.style.borderColor = '#909399';
            },

            //对每个加载/卸载模型按钮设定一个ref
            buttonRefAndID(str,ind){
                return str + ind;
            },

            //预测模型按钮
            clickPredictModel(sc){
              if(this.is_load[sc.$index]){
                this.predictModelId = sc.row.model_id;
                this.predictModelVisible = true;
              }else {
                this.$notify.info({
                  title:'提示',
                  message:'请先加载模型后再进行预测'
                });
              }
            },

            //预测dialog里的预测按钮
            startPredictButton(){
                this.predictHeaders = this.predictDataForm.headers.replace(/"/g,'').split(',');
                // 处理headers
                let tc = this.predictDataForm.headers.split('，');
                if(this.predictHeaders.length<tc.length){
                  this.predictHeaders = tc;
                }
                //处理预测的data
                let rows = [];
                let s1 = this.predictDataForm.data.replace(/"/g,'').split(';')
                if(typeof(s1) === "string"){
                  s1=[].push(s1);
                }
                for(let i=0;i<s1.length;i++){
                  let s2 = s1[i].split('\n');
                  if(typeof(s2) === "string"){
                    rows.push(s2);
                  }else {
                    for(let j=0;j<s2.length;j++){
                      rows.push(s2[j]);
                    }
                  }
                }
                for(let i=0;i<rows.length;i++){
                  let r1 = rows[i].split(',');
                  let r2 = rows[i].split('，');
                  if(r1.length<r2.length){
                    r1 = r2;
                  }
                  for(let j=1;j<r1.length;j++){
                    let rf = parseFloat(r1[j]);
                    if(!isNaN(rf)){
                      r1[j] = rf;
                    }
                  }
                  rows[i] = r1;
                }
                this.predictData = rows;
                this.predictModel(this.predictModelId);
            }
        }
    }
</script>

<style scoped>
    .el-popconfirm {
        margin-right: 10px;
    }
    #pagination {
        margin-top: 20px;
        margin-bottom: 20px;
        float: right;
    }
    .oper-btn {
        margin-left: 0;
        margin-right: 5px;
    }
</style>
